import React, { createContext, useContext, useState } from 'react';

const SolicitacaoCtx = createContext();

export function SolicitacaoProvider({ children }) {
  
  const [tela, setTela] = useState(1);
  const [qtdeTelas, setQtdeTelas] = useState(6);
  const [dados, setDados] = useState({
    idEntrega: "",
    data: "",
    cliente: "",
    tipoVeiculo: "",
    deslocamento: "",
    distancia: "", 
    valor: "",
    previsao: "",
    origCep: "",
    origLogradouro: "",
    origNumero: "",
    origComplemento: "",
    origReferencia: "",
    origBairro: "",
    origIdCidade: "",
    origCidade: "",
    origEstado: "",
    origContato: "",
    origTelefone: "",
    origTarefa: "",
    origAddFavorito: false,
    destCep: "",
    destLogradouro: "",
    destNumero: "",
    destComplemento: "",
    destReferencia: "",
    destBairro: "",
    destIdCidade: "", 
    destCidade: "",
    destEstado: "",
    destContato: "",
    destTelefone: "",
    destTarefa: "",
    destAddFavorito: false
  });
  const [pgto, setPgto] = useState({
    idPagamento: "",
    idEntrega: "",
    idTabPreco: "",
    idGateway: "",
    idRetornoGateway: "",
    idDesconto: "",
    tipoPgto: "",
    cupom: "",
    vlrPercurso: "",
    vlrProduto: "",
    vlrDesconto: "",
    vlrTotal: "", 
    strPercurso: ""
  });
  const [cartao, setCartao] = useState({
    chkNome: "", 
    chkEmail: "",
    chkNrCartao: "",
    chkCodSeguranca: "",
    chkMes: "",
    chkAno: "",
    idMeioPagamento: "",
    idTipoPagamento: "",
  });

  return (
    <SolicitacaoCtx.Provider value={{ 
      tela, setTela, qtdeTelas, setQtdeTelas, dados, setDados, 
      pgto, setPgto, cartao, setCartao
    }}>
      {children}
    </SolicitacaoCtx.Provider>
  );
}

export function useSolicitacao() {
  const context = useContext(SolicitacaoCtx);
  return context;
}