import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import { 
  Avatar, Button, Card, CardContent, CardHeader, Divider, Grid, Radio, TextField, Typography
} from '@material-ui/core';
import { ArrowBack, AssignmentInd, ImageSearch } from '@material-ui/icons';
import { useForm } from 'react-hook-form';
import { yupResolver } from '@hookform/resolvers';
import CabecalhoForm from '../../components/CabecalhoForm';
// import MyTable from '../../components/MyTable';
import * as service from '../../services/AprovacaoService';
import { useAlerta } from '../../contexts/AlertaCtx';
import { useGeral } from '../../contexts/GeralCtx';
import PageCss from '../PagesCss';
import schema from './AprovacaoSch';
import { TIPO_ERRO, TIPO_SUCESSO } from '../../utils/global';
import MaterialTable from 'material-table';


export default function AprovacaoForm() {
  
  const css = PageCss();
  const history = useHistory();
  const { setConteudo } = useAlerta();
  const [selectedValue, setSelectedValue] = useState();
  const [dados, setDados] = useState([]);
  const { id, alterar, setAlterar, setCarregar, estiloDeCampo } = useGeral();
  const valoresIniciais = { pessoa: {id: null, nome: ''}, statusAprovacao: '' };
  const { register, handleSubmit, reset, setValue, errors } = useForm({
    mode: 'all',
    defaultValues: valoresIniciais,
    resolver: yupResolver(schema)
  });

  const colunasTabFotos = [
    { title: 'ID', field: 'id', width: 20, cellStyle: {fontSize: 12} },
    { title: 'Membro', field: 'pessoa.id', width: 20, hidden: true, cellStyle: {fontSize: 12} },
    { title: 'Tipo Foto', field: 'tipoFoto', width: 50, cellStyle: {fontSize: 12} },
    { title: 'Nome Arquivo', field: 'nomeArquivo', width: 50, hidden: true, cellStyle: {fontSize: 12} },
    { title: 'Descrição', field: 'descricao', width: 80, cellStyle: {fontSize: 12} },
    { title: 'Tipo Conteúdo', field: 'tipoConteudo', width: 40, hidden: true, cellStyle: {fontSize: 12} },
    { title: 'Tamanho', field: 'tamanho', width: 40, hidden: true, cellStyle: {fontSize: 12} },
    { title: 'Link', field: 'link', width: 1000, cellStyle: {fontSize: 12} },    
  ]

  const handleChange = (event) => {
    setSelectedValue(event.target.value);
    setValue('statusAprovacao', event.target.value);
  };

  useEffect(() => {
    if (alterar) {
      async function buscaDado() {
        try {
          let idPessoa = null;
          const resposta = await service.obtem(`/aprovacoes/${id}?campos=pessoa.id,pessoa.nome,statusAprovacao`);
          if (resposta.data) {
            idPessoa = resposta.data.pessoa.id;
            setValue('pessoa.id', resposta.data.pessoa.id);
            setValue('pessoa.nome', resposta.data.pessoa.nome);
            setValue('statusAprovacao', resposta.data.statusAprovacao);
            setSelectedValue(resposta.data.statusAprovacao);
          }

          service.obtem(`/fotos/pessoa/${idPessoa}`)
          .then(response => {
            setDados(response.data);
          })
          .catch(error => {
            setConteudo({
              tipo: TIPO_ERRO,
              descricao: error.response.data.detalhe,
              exibir: true
            });
          }) 
        } catch (error) {
          setConteudo({
            tipo: TIPO_ERRO,
            descricao: error.response.data.detalhe,
            exibir: true
          });
        }
      }
      buscaDado();
    }
  }, [id, alterar, setConteudo, setValue])

  function submitForm(data, e) {
    if (alterar & id > 0) {
      async function alteraDado() {
        try {
          const resposta = await service.altera(`/aprovacoes/altera-status/${id}`, data);
          if (resposta) {
            setConteudo({
              tipo: TIPO_SUCESSO,
              descricao: 'Registro alterado com sucesso!',
              exibir: true
            });
          }
          setAlterar(false);
          setCarregar(true);
          history.push('/aprovacoes');
        } catch (error) {
          setConteudo({
            tipo: TIPO_ERRO,
            descricao: error.response.data.detalhe,
            exibir: true
          });
        }
      }
      alteraDado();
    } 
  }

  return (
    <div>
      <CabecalhoForm
        titulo="Aprovação"
        subtitulo="Adição e/ou Edição de registro"
        linkPagina="/aprovacoes"
        icone={<ArrowBack />}
        tituloBotao="Retornar"
      />

      <form onSubmit={handleSubmit(submitForm)}>
        <Card variant="outlined">
          <CardContent>
            <Card>
              <CardHeader
                avatar={<Avatar className={css.avatar}><AssignmentInd /></Avatar>}
                title="Aprovação"
                subheader="Informe o status da aprovação"
                className={css.cartaoTitulo}
              />
              <CardContent>
                <Grid container direction="row" spacing={2}>

                  <Grid item xs={12} sm={2} >
                    <TextField
                      fullWidth
                      disabled
                      type="number"
                      name="pessoa.id"
                      label="Membro"                      
                      variant={estiloDeCampo}
                      inputRef={register}
                      placeholder="EX.: 1"
                      InputLabelProps={{ shrink: true }}
                    />
                  </Grid>

                  <Grid item xs={12} sm={10}>
                    <TextField
                      fullWidth
                      disabled
                      type="text"
                      name="pessoa.nome"
                      label="Nome do Membro"
                      variant={estiloDeCampo}
                      inputRef={register}
                      InputLabelProps={{ shrink: true }}
                      inputProps={{ style: { textTransform: "uppercase" } }}
                    />
                  </Grid>

                  <Grid container direction="row" alignItems="center" spacing={2}>
                    <Grid item xs={12} sm={12}>
                      <div style={{marginLeft: '10px', marginTop: '10px'}}>
                        <Typography variant="body2">Status Aprovação:</Typography>
                      </div>
                      <div style={{display: 'flex', flexDirection: 'row', alignItems: 'center' }}>
                        <Radio
                          name="statusAprovacao"
                          value="P"
                          checked={selectedValue === 'P'}
                          onChange={handleChange}
                          inputRef={register}
                        />
                        <Typography variant="body2">Pendente</Typography>
                        <Radio
                          name="statusAprovacao"
                          value="E"
                          checked={selectedValue === 'E'}
                          onChange={handleChange}
                          inputRef={register}
                        />
                        <Typography variant="body2">Em análise</Typography>
                        <Radio
                          name="statusAprovacao"
                          value="A"
                          checked={selectedValue === 'A'}
                          onChange={handleChange}
                          inputRef={register}
                        />
                        <Typography variant="body2">Aprovado</Typography>
                        <Radio
                          name="statusAprovacao"
                          value="R"
                          checked={selectedValue === 'R'}
                          onChange={handleChange}
                          inputRef={register}
                        />
                        <Typography variant="body2">Rejeitado</Typography>
                        <Radio
                          name="statusAprovacao"
                          value="S"
                          checked={selectedValue === 'S'}
                          onChange={handleChange}
                          inputRef={register}
                        />
                        <Typography variant="body2">Suspenso</Typography>
                      </div>
                    </Grid>
                    <Grid item xs={12} sm={12}>
                      <div style={{ color: 'red', marginLeft: '10px' }}>
                        {errors.statusAprovacao ? errors.statusAprovacao.message : ''}
                      </div>
                    </Grid>
                  </Grid>

                </Grid>

                <Card>
                  <CardHeader
                    avatar={<Avatar className={css.avatar}><ImageSearch /></Avatar>}
                    title="Imagens"
                    subheader="Listagem das imagens a serem analisadas"
                    className={css.cartaoTitulo}
                  />                  
                  <MaterialTable
                    isLoading={false}
                    columns={colunasTabFotos}
                    data={dados}
                    options={{
                      padding: "dense",
                      showTitle: false,
                      filtering: false,
                      grouping: false,
                      columnsButton: false,
                      pageSize: 5,
                      searchFieldAlignment: "left",
                      searchFieldVariant: "standard",
                      actionsColumnIndex: -1,
                      loadingType: "linear",
                      showTextRowsSelected: false,
                      toolbarButtonAlignment: "left",
                      exportButton: false,
                      selection: false,
                      headerStyle: {
                        backgroundColor: '#44575F',
                        color: '#FFF',
                      },
                    }}
                    localization={{
                      body: {
                        emptyDataSourceMessage: 'Nenhum registro para exibir...'
                      },
                      toolbar: {
                        addRemoveColumns: 'Adiciona ou remove colunas',
                        searchTooltip: 'Pesquisar',
                        searchPlaceholder: 'Pesquisar',
                        nRowsSelected: '{0} linha(s) selecionada',
                        showColumnsTitle: 'Mostrar colunas',
                        showColumnsAriaLabel: 'Mostrar colunas',
                        exportTitle: 'Exportar',
                        exportAriaLabel: 'Exportar',
                        exportName: 'Exportar como CSV'
                      },
                      header: {
                        actions: 'Ações'
                      },
                      pagination: {
                        labelRowsSelect: 'linhas',
                        labelDisplayedRows: '{count} de {from}-{to}',
                        firstTooltip: 'Primeira página',
                        previousTooltip: 'Página anterior',
                        nextTooltip: 'Próxima página',
                        lastTooltip: 'Última página'
                      }
                    }}
                    actions={[
                      {
                        icon: 'link',
                        tooltip: 'Visualizar Imagem',
                        onClick: (event, rowData) => {
                          window.open(rowData.link, "_blank");
                        },
                      }
                    ]}
                  />
                </Card>
              </CardContent>
            </Card>

            <div className={css.formBarraBotao}>
              <Divider />
            </div>

            <div style={{ display: 'flex' }}>
              <div style={{ flex: 1 }} />
              <Button
                type="submit"
                variant="contained"
                color="primary"
                disableElevation
                className={css.formBotaoDeAcao}
              > Salvar
              </Button>
              <Button
                type="reset"
                variant="contained"
                color="primary"
                disableElevation
                onClick={() => reset(valoresIniciais)}
                className={css.formBotaoDeAcao}
              > Limpar
              </Button>
            </div>
            
          </CardContent>
        </Card>
      </form>
    </div>
  )
}
