import React from 'react';
import { Typography } from '@material-ui/core';
import style from '../../layout/styles';
//import cssDashboard from '../Dashboard/styles';
import BoxEntrega from './BoxEntrega';
import BoxPagamento from './BoxPagamento';



export default function Dashboard() {

  const estilo = style();

  return (
    <div>
      <div className={estilo.tituloPagina}>
        <Typography variant="h6">Dashboard</Typography>
      </div>
      <div className={estilo.subtituloPagina}>
        <Typography variant="caption">Oferece visualização rápida dos principais indicadores de desempenho</Typography>
      </div>
      <br />

      <BoxEntrega />
      <BoxPagamento />

    </div>
  );
}
